import { configureStore, createSlice } from '@reduxjs/toolkit';


let cart=createSlice({
    name:"cart",
    initialState:[
        {id:22, name:"    400ml" ,count:1, price:13200,imgUrl:"img/store6.jpg"},
        {id:23, name:"   50ml" ,count:1, price:24000,imgUrl:"img/store2.jpg"},
        {id:24, name:"    50ml" ,count:2, price:18200,imgUrl:"img/store3.jpg"},
        {id:25, name:"    1" ,count:3, price:3000,imgUrl:"img/store4.jpg"},
        {id:26, name:"   150ml" ,count:1, price:17000,imgUrl:"img/store5.jpg"},
        {id:27, name:"    (100ml+10ml*2)" ,count:1, price:22400,imgUrl:"img/store1.jpg"},
    ],
    reducers:{
        addCount(state,action){
            let num=state.findIndex((a)=>{return a.id===action.payload})
            state[num].count++
        },
        minusCount(state,action){
            let num=state.findIndex((a)=>{return a.id===action.payload})
            state[num].count--
            if(state[num].count<=0){
                alert("0    .");
                state[num].count=0;
            }
        },
        addItem(state,action){
            state.push(action.payload)
        },
        sortName(state,action){
            state.sort((a,b)=>
            a.name>b.name?1:-1)
        }
    }
})

