import { useState,useEffect } from 'react';
import { Container,Row,Col,Button,Nav,Table,Accordion} from 'react-bootstrap';
import { useParams,useNavigate} from 'react-router-dom';
import { addItem } from '../store';
import { useDispatch } from 'react-redux';

function Detail(props){
  let {id}=useParams();
  let selproduct=props.cosmetic.find((x)=>x.id==id);
  let navigate=useNavigate();
  let [tab,setTab]=useState(0);

  let[scale,setScale]=useState('');
  useEffect(()=>{
    setTimeout(()=>{setScale('end')},50)
    return()=>{
      setScale('');
    }
  },[tab])

  let dispatch=useDispatch();

  const [num1,setNum1]=useState(1);
  const decrease=()=>{
    setNum1(num1 -1);
    if(num1<=0){
      alert("0    ");
      setNum1(0);
    }
  }

  useEffect(()=>{
    setTimeout(()=>{setScale('end')},50)
    return()=>{
      setScale('');
    }
  },[tab])

  return(
    <>
    <div className={'start '+scale}>
      <div style={{background:"darkred",textAlign:'center',color:"white",width:"100%",height:"30px",fontSize:"20px",marginBottom:"60px"}}>
        
      </div>
      <Container style={{marginBottom:"60px"}}>
      <Row xs={1} sm={1} md={2}  style={{marginBottom:"60px"}}>
        <Col style={{textAlign:'center'}}>
          <img src={process.env.PUBLIC_URL+selproduct.imgUrl} alt="product"></img>
        </Col>
        <Col >
          <p style={{color:"darkred",fontSize:"25px",fontWeight:"bold"}}>{selproduct.title}</p>
          <p style={{fontSize:"16px"}}>{selproduct.text1}</p>
          <p style={{fontSize:"25px",fontWeight:"bold"}}>{selproduct.price}&nbsp;&nbsp;<span style={{color:"#ccc",fontSize:"16px",textDecoration:"line-through"}}>{selproduct.content}</span></p>
          <table style={{fontSize:"16px"}}>
            <tbody>
            <tr>
              <th style={{width:"30%"}}></th><td style={{cursor:"pointer"}}></td>
            </tr>
            <tr>
              <th></th><td>2022.06.11</td>
            </tr>
            <tr>
              <th></th><td> 2,   1</td>
            </tr>
            </tbody>
          </table>
          <hr style={{borderBottom:"2px solid darkred"}}></hr>
          <div style={{textAlign:"right"}}>
            <Button variant="outline-danger" className="priceBtn" style={{width:"40px"}} onClick={decrease}>-</Button>{' '}

            <Button variant="outline-danger"  className="priceBtn2" style={{width:"40px"}} disabled="disabled">{num1}</Button>{' '}

            <Button variant="outline-danger" className="priceBtn" style={{width:"40px"}} onClick={()=>setNum1(num1 +1)}>+</Button>{' '}

            <Button variant="outline-danger" className="priceBtn2" disabled="disabled">{num1*selproduct.price+" "}</Button>{' '}
          </div>
          <div style={{display:"flex"}} id="shopbtn1">
            <Button variant="outline-danger" onClick={()=>{dispatch(addItem({id:selproduct.id,name:selproduct.title,count:1*num1,imgUrl:selproduct.imgUrl,price:selproduct.price}))}}>  </Button>{' '}
            <Button variant="outline-danger">  </Button>{' '}
            <Button variant="outline-danger">+ </Button>{' '}
          </div>
        </Col>
      </Row>
      <div style={{background:'#fbf4d7',height:"40px",justifyContent:'center',color:"darkred",fontSize:"18px",alignItems:"center",display:"flex",marginBottom:"10px"}}>+ X   5   +</div>
      <div style={{background:'darkred',height:"40px",justifyContent:'center',color:"white",fontSize:"18px",alignItems:"center",display:"flex"}}>   + 3000    </div>
      <hr style={{borderBottom:"2px solid darkred"}}></hr>
      
      <Nav variant="tabs" defaultActiveKey="link0">
      <Nav.Item>
        <Nav.Link onClick={()=>{setTab(0)}}></Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link onClick={()=>{setTab(1)}}>/ </Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link onClick={()=>{setTab(2)}}></Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link onClick={()=>{setTab(3)}}></Nav.Link>
      </Nav.Item>
      </Nav>

      <TabContent tab={tab} selproduct={selproduct}/>
        
      </Container>
      
    </div>
    </>
  )
}

